.App {
  text-align: center;
  background: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

button {
  border: none;
  background: none;
  margin-top: 0px;
}

p {
  margin-bottom: 5px;
  line-height: 1.4em;
}
input[type=text] {
  width: 80%;
  padding: 12px 20px;
  margin: 8px 4px;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.5);
}
